import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {dataService} from '../../dataservice.service'
import { Router } from "@angular/router";

@Component({
  selector: 'app-page-process',
  templateUrl: './page-process.component.html',
  styleUrls: ['./page-process.component.css']
})
export class PageProcessComponent implements OnInit {

  data:any;
  lines = [];
  dtOptions: DataTables.Settings = {};
  sampleNumber:any;
  lastDigit:any;
  display_column_1 = 'none';
  display_column_2 = 'none';
  display_column_3 = 'none';
  display_column_4 = 'none';
  display_column_5 = 'none';
  headersData=[];
  constructor(private service:dataService,private http:HttpClient,private router:Router) { }

  ngOnInit() {
    this.data = this.service.labels;
    
    if(this.data == ''){
      this.router.navigateByUrl('/')
    }

    console.log(this.data);

    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 500,
      processing: true,
      lengthChange:false,
      "searching": false,
    };

    this.http.get('assets/SAMPLE_INPUT.txt', { responseType: 'text' as 'json'}).subscribe(
      (res:any) =>{

        let allTextLines = (<string>res).split(/\r\n/);
        let headers = allTextLines[0].split(',');

        let headersRow = [this.data.label2,this.data.label3,this.data.label4];

        for(let index=0;index<headersRow.length;index++){
            if(headersRow[index] != ''){
              this.headersData.push(headersRow[index]);
            }
        }

        for ( let i = 0; i < allTextLines.length; i++) {
            // split content based on comma
            let data = allTextLines[i].split(',');
          
            if (data.length == headers.length) {
              
                let tarr = [];
                
                for ( let j = 0; j < headers.length; j++) { 
                                  
                    if(j == 0){
                      this.sampleNumber = data[j];
                      this.lastDigit = this.sampleNumber % 10;

                      tarr['background']='white';
                      tarr['color']='gray';

                      if(this.lastDigit === 1){
                        tarr['background']='red';
                        tarr['color']='green';
                      }
                      
                      if(this.lastDigit === 2){
                        tarr['background']='white';
                        tarr['color']='#0000FF';
                      }
                      
                      if(this.lastDigit === 0){
                        tarr['background']='white';
                        tarr['color']='red';
                      }                      
                    }
                    
                    /***********condition folr label 2 */
                    if(this.data.label2 === 'column_0' && j==0){
                       tarr.push(data[j]);
                    }else if(this.data.label2 === 'column_1' && j==1){
                        tarr.push(data[j]);
                    }else if(this.data.label2 === 'column_2' && j==2){
                      tarr.push(data[j]);
                    }else if(this.data.label2 === 'column_3' && j==3){
                      tarr.push(data[j]);
                    }else if(this.data.label2 === 'column_4' && j==4){
                      tarr.push(data[j]);
                    }  
                    
                    /***********condition folr label 3 */

                    if(this.data.label3 === 'column_0' && j==0){
                      tarr.push(data[j]);
                   }else if(this.data.label3 === 'column_1' && j==1){
                       tarr.push(data[j]);
                   }else if(this.data.label3 === 'column_2' && j==2){
                     tarr.push(data[j]);
                   }else if(this.data.label3 === 'column_3' && j==3){
                     tarr.push(data[j]);
                   }else if(this.data.label3 === 'column_4' && j==4){
                     tarr.push(data[j]);
                   }  


                  /***********condition folr label 4 */

                 if(this.data.label4 === 'column_0' && j==0){
                    tarr.push(data[j]);
                 }else if(this.data.label4 === 'column_1' && j==1){
                     tarr.push(data[j]);
                 }else if(this.data.label4 === 'column_2' && j==2){
                   tarr.push(data[j]);
                 }else if(this.data.label4 === 'column_3' && j==3){
                   tarr.push(data[j]);
                 }else if(this.data.label4 === 'column_4' && j==4){
                   tarr.push(data[j]);
                 }  
           
                }
                
              this.lines.push(tarr);
            }
        }
      })
  }

}
