import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, FormGroup, Validators,FormControl } from '@angular/forms';
import {dataService} from '../dataservice.service';
import { Router } from "@angular/router";
declare let $: any;
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {

  dtOptions: DataTables.Settings = {};
  sampleNumber:any;
  lastDigit:any;
  formDetails: FormGroup;
  lines = [];

  constructor(private http:HttpClient,private formBuilder: FormBuilder,private service:dataService,private router:Router) { }
  
  ngOnInit() {

    this.formDetails = this.formBuilder.group({        
      label2: [''],
      label3:  [''],
      label4:['']
    });

    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 500,
      processing: true,
      lengthChange:false,
      "searching": false,
    };

    this.http.get('assets/SAMPLE_INPUT.txt', { responseType: 'text' as 'json'}).subscribe(
      (res:any) =>{

        let allTextLines = (<string>res).split(/\r\n/);
        let headers = allTextLines[0].split(',');

        for ( let i = 0; i < allTextLines.length; i++) {
            // split content based on comma
            let data = allTextLines[i].split(',');
          
            if (data.length == headers.length) {
                let tarr = [];
                for ( let j = 0; j < headers.length; j++) {
                    if(j == 0){
                      this.sampleNumber = data[j],
                      this.lastDigit = this.sampleNumber % 10;

                      tarr['background']='white';
                      tarr['color']='gray';

                      if(this.lastDigit === 1){
                        tarr['background']='red';
                        tarr['color']='green';
                      }
                      
                      if(this.lastDigit === 2){
                        tarr['background']='white';
                        tarr['color']='#0000FF';
                      }
                      
                      if(this.lastDigit === 0){
                        tarr['background']='white';
                        tarr['color']='red';
                      }
                      
                    }
                    tarr.push(data[j]);
                }
                this.lines.push(tarr);
            }
        }
      })
  }

  eventEmitDoubleClick(event){
      this.service.labels = event;
      this.router.navigateByUrl('/page-single');
  }

  buttonInRowClick(event: any): void {
    event.stopPropagation();
  }

  wholeRowClick(): void {
  }

  nextButtonClickEvent(): void {
  }
  
  previousButtonClickEvent(): void {
  }

  submit(){
      let params:any = {  
        'label2':this.formDetails.value.label2,   
        'label3':this.formDetails.value.label3,   
        'label4':this.formDetails.value.label4      	
      }
      params
      this.service.labels = params;
      this.router.navigateByUrl('/page-process');
  }
  
}
