import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexComponent } from './index/index.component';
import { PageButtonComponent } from './pages/page-button/page-button.component';
import { PageProcessComponent } from './pages/page-process/page-process.component';
import { PageSingleRowComponent } from './pages/page-single-row/page-single-row.component';

const routes: Routes = [
  {
    path: '',
    component: IndexComponent,   
  }, 
  {
    path: 'page-button',
    component: PageButtonComponent,   
  }, 
  {
    path: 'page-process',
    component: PageProcessComponent,   
  }, 
  {
    path: 'page-single',
    component: PageSingleRowComponent,   
  }, 

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
